import React, { Component } from 'react';
import Masonry from 'react-masonry-component';
import styled from 'styled-components';

export default class Gellery extends Component {
  constructor(props) {
    super(props);
    this.state = {
      images: this.props.content,
      imagesFirst: this.props.content.slice(0, 19),
      imagesLast: this.props.content.slice(19),
      showAdditional: false
    };
  }

  renderImages(img) {
    const childElements = img.map((img, index) => (
      <div className="col-6 col-md-4 col-lg-3  image-element-class" key={index}>
        <GalleryImg
          srcSet={`
              ${img.sizes.large} 1x,
              ${img.url} 2x
            `}
          src={img.url}
          alt={img.alt}
        />
      </div>
    ));
    return childElements;
  }

  showMore = () => {
    this.setState({
      imagesFirst: [...this.state.imagesFirst, ...this.state.imagesLast],
      showAdditional: true
    });
  };

  render() {
    let { imagesFirst, showAdditional } = this.state;

    return (
      <React.Fragment>
        <Masonry
          className="row my-gallery-class" // default ''
          elementType="div" // default 'div'
          options={masonryOptions} // default {}
          disableImagesLoaded={false} // default false
          updateOnEachImageLoad={true} // default false and works only if disableImagesLoaded is false
          imagesLoadedOptions={imagesLoadedOptions} // default {}
        >
          {this.renderImages(imagesFirst)}
        </Masonry>
        {showAdditional || (
          <React.Fragment>
            <div className="row text-center pb-5 pt-5">
              <div className="col-12">
                <BtnShowMore onClick={this.showMore}>
                  ПРОСМОТРЕТЬ БОЛЬШЕ
                </BtnShowMore>
              </div>
            </div>
          </React.Fragment>
        )}
      </React.Fragment>
    );
  }
}

const GalleryImg = styled.img`
  width: 100%;
  object-fit: cover;
`;

const BtnShowMore = styled.button`
  border: none;
  background: none;
  display: inline-block;
  padding: 12px 17px;
  letter-spacing: 0.2em;
  border: 3px solid #000018;
  color: #000018;
  font-weight: 700;
  transition: background-color .5s;
  font-family: inherit;
  font-size: 15px;

  &:hover{
    background-color: #b29879;

  }
`;

const masonryOptions = {
  transitionDuration: '0.5s'
};

const imagesLoadedOptions = { background: '.my-bg-image-el' };
